import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import {connect} from 'react-redux'

import QuestionList from './components/QuestionList'
import {initializeQuestions} from './reducers/questionReducer'
import {changeLanguage} from './reducers/languageReducer'

import ResultList from './components/ResultList'
import Home from './components/Home'
import {Table} from 'react-bootstrap'

class App extends React.Component {

  componentWillMount = async () => {
    await this.props.initializeQuestions(this.props.language)
  }

  render() {
    const eng = this.props.language === "EN" ? {backgroundColor : 'lightgray'} : {}
    const fi = this.props.language === "FI" ? {backgroundColor : 'lightgray'} : {}
    return (
      <div className="App">
        <div className='container'>
        <div className='languageSelection'>
          <p><a style={eng} onClick={this.languageChange}>EN</a> / <a style={fi} onClick={this.languageChange}>FI</a></p>
        </div>
          <Router>
            <div>
            <Table>
              <tbody>
                <tr>
                  <td><Link to="/"><h5>info</h5></Link></td>
                  <td><Link to="/start"><h5>start</h5></Link></td>
                </tr>
              </tbody>
            </Table>
            <Route exact path="/" render={() => <Home language={this.props.language}/>} />
            <Route exact path="/start" render={() => <QuestionList />} />
            <Route exact path="/results" render={() => <ResultList />} />
            <br/>
          </div>
        </Router>
      </div>
    </div>
  )
  }

  languageChange = async (e) => {
    const language = e.target.innerHTML
    await this.props.changeLanguage(language)
    await this.props.initializeQuestions(this.props.language)
    }
}

/*
<Table>
  <tbody>
    <tr>
      <td><Link to="/"><h5>home</h5></Link></td>
    </tr>
  </tbody>
</Table>
*/
const mapStateToProps = (state) => {
    return {
      questions: state.questions,
      answers: state.answers,
      results: state.results,
      language: state.language
    }
}

export default connect(
  mapStateToProps,
  {initializeQuestions,
  changeLanguage}
)(App)



