import React from 'react'
import '../App.css';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'

const Home = (props) => {
  const home_style = {
    backgroundColor: '#222',
    padding: 20,
    color: 'white',
    borderRadius: 4
  }
  const title = props.language === 'EN' ? <EnglishTitle/> : <FinnishTitle/>
  const infoBox = props.language === 'EN' ? <EnglishInfo/> : <FinnishInfo/>
    return(
      <div style={home_style}>
        {title}
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Pelago-Brooklyn.jpg" alt="Brooklyn" style={{width: 100, marginTop:5, marginRight:5, borderRadius: 2}}/>
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Hanko-Street.jpg" alt="Hanko Street" style={{width: 100, marginTop:5, marginRight:5, borderRadius: 2}}/>
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Stavanger_Commuter_1.jpg" alt="Stavanger Commuter" style={{marginTop:5, width: 100, borderRadius: 2}}/>
        {infoBox}
      </div>
    )
}

const EnglishTitle = () => {
  return(
    <div>
      <h2 style={{paddingBottom:5}}>Welcome to Bike Assistant</h2>
    </div>
  )
}

const FinnishTitle = () => {
  return(
    <div>
      <h2 style={{paddingBottom:5}}>Tervetuloa Pyöräsuosittelijaan</h2>
    </div>
  )
}

const FinnishInfo = () => {
  return(
    <div>
    <p style={{paddingTop: 25, fontSize: 'large'}}>Vastaa kymmeneen kysymykseen ja katso mikä malli vastaa parhaiten mieltymyksiäsi.</p>
    <p style={{paddingTop: 15, paddingBottom: 25}}>[ Voit hypätä kysymyksen yli, jos siihen tuntuu vaikealta vastata. ]</p>
    </div>
  )
}

const EnglishInfo = () => {
  return(
    <div>
      <p style={{paddingTop: 25, fontSize: 'large'}}>The idea is to give you a recommendation so that you have more than a hunch of what you are after.</p>
      <p style={{paddingTop: 15, paddingBottom: 25}}>[ If you find it challenging answering to a question, feel free to skip it. ]</p>
    </div>
  )
}
/*
<Link to="/start"><h5>start</h5></Link>

<div className='languageSelection'>
  <p>
    <a onClick={this.languageChange}>EN</a>
    <a onClick={this.languageChange}>FI</a>
  </p>
</div>
*/
export default Home



